.TodoItem {
  padding: 0px 0px 0px 15px;
  color: #FFFFFF;
  background-color: #323031;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 24px;
}

.TodoItem-p {
  font-size: 18px;
  line-height: 24px;
  font-weight: 600;
  font-family: 'Hind Siliguri', sans-serif;

}

.TodoItem-p--complete {
  text-decoration: line-through;
  opacity: 0.7;
}

.Icon {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 48px;
  width: 48px;
  font-size: 24px;
  font-weight: bold;
}

.Icon-check {
  position: absolute;
  left: 12px;
}

.Icon-check--active {
  color: rgb(0, 255, 0);
}

.Icon-delete {
  position: absolute;
  right: 12px;
  ;
}

.Icon-delete:hover {
  color: red;
}

@media only screen and (max-width: 1049px) {
  .Icon-check {
    position: absolute;
    left: -50px;
  }
  .Icon-delete{
    position: absolute;
    right: -50px;
  }
}
